.atlas {
    .center {
        text-align: center;
        vertical-align: top;
        position: relative;
        /* overflow: hidden; */

        .info {
            position: absolute;
            width: 100%;
            left: 0;
            top: 30%;

            .today {
                margin-top: 5px;
            }
        }
    }

    table {
        border-spacing: 0;
        border-collapse: collapse;
    }

    td {
        padding: 0;
        margin: 0;
        border: black 1px solid;
    }
}

.time-table {
    font-size: 0.5em;
    td {
        width: calc(100% / 12);
        padding: 2px;
        text-align: center;
    }
}