$ming-color: red;
$yun-color: green;
$year-color: blue;
$month-color: purple;
$day-color: rgb(165, 102, 24);

.palace {
    min-width: 90px;
    min-height: 110px;
    position: relative;
    font-size: 0.6em;
    padding: 2px;

    .row {
        min-height: 20px;

        .level-1-stars {
            display: inline-block;
            font-size: 1.05em;
        }

        .level-2-stars {
            display: inline-block;
            position: absolute;
            right: 2px;
            font-size: 0.95em;
        }
    }

    .yun-row {
        position: absolute;
        right: 0;
        top: 4em;
    }

    .ming {
        color: $ming-color;
    }

    .yun {
        color: $yun-color;
    }

    .year {
        color: $year-color;
    }

    .month {
        color: $month-color;
    }

    .day {
        color: $day-color;
    }

    .bottom-row {
        position: absolute;
        bottom: 2px;
        left: 0;
        width: 100%;

        .palace-name {
            display: inline-block;
            padding-left: 2px;
        }

        .right-col {
            display: inline;
            position: absolute;
            right: 0;
            bottom: 0;

            .da-xian {
                font-size: 0.8em;
                margin-right: 5px;
            }
        
            .di-zhi {
                padding-right: 2px;
                writing-mode: vertical-lr;
                font-size: 1.1em;
            }
        }
    }

    .shen-palace {
        color: red;
        font-weight: bold;
        border: red 1px solid;
    }

    .star-sign-info {
        writing-mode: vertical-lr;
        vertical-align: top;

        .bright {
            font-size: 0.9em;
            padding: 1px;
            display: inline-block;
            height: 1em;
        }

        .si-hua {
            font-size: 0.9em;
            font-weight: bold;
            color: white;
            display: inline-block;
            height: 1em;
            padding: 1px 0;
            margin-top: 1px; 

            &.ming {
                background: $ming-color;
            }

            &.yun {
                background: $yun-color;
            }

            &.year {
                background: $year-color;
            }

            &.month {
                background: $month-color;
            }

            &.day {
                background: $day-color;
            }
        }

        .star-sign {
            padding: 1px 0 1px 0;
            
            &.lu {
                color: white;
                font-weight: bold;
                background: green;
            }
        
            &.quan {
                color: white;
                font-weight: bold;
                background: purple;
            }
        
            &.ke {
                color: white;
                font-weight: bold;
                background: blue;
            }
        
            &.ji {
                color: white;
                font-weight: bold;
                background: red;
            }
        }
    }
}