.da-xian {
    text-align: center;

    .dx-block {
        display: inline-block;
        vertical-align: bottom;
        border-radius: 5px;
        
        .dx-selector {
            margin: 1px;
            max-width: 100px;
        }

        .dx-enabler {
            text-align: center;
            font-size: 0.7em;
            
            padding: 2px;
            margin: 1px;

            border: 1px rgb(166, 166, 166) solid;
            border-radius: 5px;

            cursor: pointer;
            user-select: none;
        }

        .dx-clear {
            font-size: 0.7em;
            line-height: 3em;
            
            padding: 2px 4px;
            margin: 1px;

            border: 1px rgb(166, 166, 166) solid;
            border-radius: 5px;

            cursor: pointer;
        }
    }

    .dx-enabled {
        background-color: lightblue;
    }
}