body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.board {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row;
          flex-flow: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap
}

.board > div {
  margin: 10px 10px 0 10px;
}
.atlas .center {
  text-align: center;
  vertical-align: top;
  position: relative;
  /* overflow: hidden; */ }
  .atlas .center .info {
    position: absolute;
    width: 100%;
    left: 0;
    top: 30%; }
    .atlas .center .info .today {
      margin-top: 5px; }

.atlas table {
  border-spacing: 0;
  border-collapse: collapse; }

.atlas td {
  padding: 0;
  margin: 0;
  border: black 1px solid; }

.time-table {
  font-size: 0.5em; }
  .time-table td {
    width: calc(100% / 12);
    padding: 2px;
    text-align: center; }

.palace {
  min-width: 90px;
  min-height: 110px;
  position: relative;
  font-size: 0.6em;
  padding: 2px; }
  .palace .row {
    min-height: 20px; }
    .palace .row .level-1-stars {
      display: inline-block;
      font-size: 1.05em; }
    .palace .row .level-2-stars {
      display: inline-block;
      position: absolute;
      right: 2px;
      font-size: 0.95em; }
  .palace .yun-row {
    position: absolute;
    right: 0;
    top: 4em; }
  .palace .ming {
    color: red; }
  .palace .yun {
    color: green; }
  .palace .year {
    color: blue; }
  .palace .month {
    color: purple; }
  .palace .day {
    color: #a56618; }
  .palace .bottom-row {
    position: absolute;
    bottom: 2px;
    left: 0;
    width: 100%; }
    .palace .bottom-row .palace-name {
      display: inline-block;
      padding-left: 2px; }
    .palace .bottom-row .right-col {
      display: inline;
      position: absolute;
      right: 0;
      bottom: 0; }
      .palace .bottom-row .right-col .da-xian {
        font-size: 0.8em;
        margin-right: 5px; }
      .palace .bottom-row .right-col .di-zhi {
        padding-right: 2px;
        -webkit-writing-mode: vertical-lr;
                writing-mode: vertical-lr;
        font-size: 1.1em; }
  .palace .shen-palace {
    color: red;
    font-weight: bold;
    border: red 1px solid; }
  .palace .star-sign-info {
    -webkit-writing-mode: vertical-lr;
            writing-mode: vertical-lr;
    vertical-align: top; }
    .palace .star-sign-info .bright {
      font-size: 0.9em;
      padding: 1px;
      display: inline-block;
      height: 1em; }
    .palace .star-sign-info .si-hua {
      font-size: 0.9em;
      font-weight: bold;
      color: white;
      display: inline-block;
      height: 1em;
      padding: 1px 0;
      margin-top: 1px; }
      .palace .star-sign-info .si-hua.ming {
        background: red; }
      .palace .star-sign-info .si-hua.yun {
        background: green; }
      .palace .star-sign-info .si-hua.year {
        background: blue; }
      .palace .star-sign-info .si-hua.month {
        background: purple; }
      .palace .star-sign-info .si-hua.day {
        background: #a56618; }
    .palace .star-sign-info .star-sign {
      padding: 1px 0 1px 0; }
      .palace .star-sign-info .star-sign.lu {
        color: white;
        font-weight: bold;
        background: green; }
      .palace .star-sign-info .star-sign.quan {
        color: white;
        font-weight: bold;
        background: purple; }
      .palace .star-sign-info .star-sign.ke {
        color: white;
        font-weight: bold;
        background: blue; }
      .palace .star-sign-info .star-sign.ji {
        color: white;
        font-weight: bold;
        background: red; }

.da-xian {
  text-align: center; }
  .da-xian .dx-block {
    display: inline-block;
    vertical-align: bottom;
    border-radius: 5px; }
    .da-xian .dx-block .dx-selector {
      margin: 1px;
      max-width: 100px; }
    .da-xian .dx-block .dx-enabler {
      text-align: center;
      font-size: 0.7em;
      padding: 2px;
      margin: 1px;
      border: 1px #a6a6a6 solid;
      border-radius: 5px;
      cursor: pointer;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none; }
    .da-xian .dx-block .dx-clear {
      font-size: 0.7em;
      line-height: 3em;
      padding: 2px 4px;
      margin: 1px;
      border: 1px #a6a6a6 solid;
      border-radius: 5px;
      cursor: pointer; }
  .da-xian .dx-enabled {
    background-color: lightblue; }

